	/*S 初始数据加载*/
	onLoad(options) {
		var data  ={
			openid: "o_fxe6I643yl2BWEq5nH91ivFMzY",
			nickname: "古嫤九年",
			sex: 2,
			language: "zh_CN",
			city: "",
			province: "",
			country: "",
			headimgurl: "https://thirdwx.qlogo.cn/mmopen/vi_32/FRz8685UjwO6LFY9M7RJ2W5uEiart6daYUwMLibZAIficAjibtmfcgAqdicBof8OArPSD3wGaycFgeYre11HVqdJ1Lw/132",
			privilege: [],
		}
		uni.setStorageSync('userInfo', data);
		uni.setStorageSync('openid',data.openid)
		// let access = uni.getStorageSync('access');
		// alert(options.code);
		// if (!access) {
		// 	this.goOauth();
		// 	this.redirectCode = options.code;
		// 	if (this.redirectCode) {
		// 		alert(444);
		// 		this.getAccessToken();
		// 	} else {
		// 		alert(666);
		// 		this.goOauth();
		// 	}
		// }
		this.handler_auth();
	},
	/*E 初始数据加载*/
	onShow() {},
	created() {},
	methods: {
		/*S  获取 微信用户信息 */
		/* S 获取 code */
		goOauth() {
			const callbackURL = encodeURIComponent('http://seats.easy8in.top/login/test'); //测试
			const wechatId = 'wx731bbb761b6df538'; //测试
			const redirectURI = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${wechatId}&redirect_uri=${callbackURL}&response_type=code&scope=snsapi_userinfo&state=STATE`;
			window.location.href = redirectURI;
		},
		getCode(name) {
			var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
			var r = window.location.search.substr(1).match(reg);
			if (r != null) return decodeURI(r[2]);
			return null;
		},
		/*  S 获取acce_token */
		getAccessToken() {
			var _this = this;
			this.$minApi
				.wanteng_wechat({
					code: _this.redirectCode
				})
				.then(res => {
					uni.setStorageSync('access', res);
					uni.setStorageSync('openid',res.openid)
					_this.openId = res.openid;
					_this.getInfoUser();
				});
		},
		getInfoUser() {
			var _this = this;
			this.$minApi
				.wanteng_userinfo({
					openid: _this.openId
				})
				.then(res => {
					uni.setStorageSync('userInfo', res);
				});
		},
		/* E 获取 微信用户信息 */
		/* S 授权 */
		handler_auth() {
			this.$minApi
				.authorization({
					/* applicationAppKey: '5dd6631e-4a28-4b5c-aa0d-cc21f89d83d8',
					applicationAppSecret: '5985097316498578588852607792443512769076941291289741597384282823' */
					applicationAppKey: 'bea00620-c83a-4ecf-b762-b7dc7f8cf387',
					applicationAppSecret: '2835268052358449330564851535334193308346662441761957035157096666'
				})
				.then(res => {
					console.log(res);
					if (res.code == 200) {
						uni.setStorageSync('WT-APP-TOKEN', res.data);
					} else {
						uni.showToast({
							title: res.message
						});
					}
				});
		},
		/* S便民服务 */
		handler_presonal() {
			window.open('#','_self')
		}
